export default navigation => {
  if (navigation.length) {
    return (
      <div className="navigation">
        {navigation.map((nav, index) => {
          return (
            <a key={index} className="nav-item" href={`#${index}`}>
              {nav}
            </a>
          );
        })}
      </div>
    );
  } else {
    return null;
  }
};
